#loading-tree{min-height:20vh;background-color:transparent;filter:hue-rotate(90deg);display:none;position:absolute;justify-content:left;margin-left:40px;align-items:center}.loader{position:relative;transform:rotate(calc(var(--r) * 45deg))}.loader span{position:absolute;top:40px;left:-20px;width:20px;height:2px;transform-origin:right;transform:rotate(calc(var(--i) * 18deg))}.loader span::before{content:"";display:block;width:15px;height:15px;background-color:#00ff0a;border-radius:15px;box-shadow:0 0 5px #00ff0a,0 0 10px #00ff0a,0 0 20px #00ff0a,0 0 30px #00ff0a,0 0 40px #00ff0a,0 0 50px #00ff0a;animation:animate 5s linear infinite;animation-delay:calc(-0.5s * var(--i))}@keyframes animate{0%{transform:translateX(100px) scale(1);opacity:0}10%{opacity:1}80%{opacity:1}100%{transform:translateX(0) scale(0);opacity:1}}#loading-tree .loader{scale:0.3}