table{width:100%;border-collapse:collapse;border:3px solid #fff}table th,table td{border:1px solid #fff;padding:10px;vertical-align:middle;background-color:transparent;color:#fff}table th{background-color:#33333385;color:#fff}table thead{border-collapse:collapse;border-spacing:0}table thead td{height:0}table thead th{display:none;height:0;border:0}table thead th:first-child{border-left:0}table thead th:last-child{border-right:0}table thead th:not(:first-child):not(:last-child){border-left:0;border-right:0}table thead th:first-child:nth-last-child(2),table thead th:first-child:nth-last-child(2)~th{border-bottom:3px solid}table tbody tr>*{border:1px solid #fff;padding:5px;background-color:transparent}table tbody tr table,table tbody tr ul,table tbody tr ol,table tbody tr blockquote,table tbody tr img,table tbody tr svg{width:100%;margin:0;padding:0}table tbody tr table{border:1px solid #fff;margin-top:5px;margin-bottom:5px}table tbody tr ul,table tbody tr ol{list-style:none;padding-left:10px;margin-top:5px;margin-bottom:5px}table tbody tr blockquote{border-left:4px solid #333;padding:10px;margin:5px 0}table tbody tr img,table tbody tr svg{max-width:100%;height:auto}